import request from '@/utils/request'

export function getMemberCardList() {//
	return request({
		url: '/member/cards',
		method: 'get',
	})
}

export function getPaymentChannelList() {//
	return request({
		url: '/payment/channels',
		method: 'get'
	})
}

export function submitOrders(data) {//
	return request({
		url: '/member/orders',
		method: 'post',
		data
	})
}
export function submitOrdersCashfree(data) {//
	return request({
		url: '/member/orders/cashfree',
		method: 'post',
		data
	})
}

export function payOrders(data) {//
	return request({
		url: `/orders/${data.order_id}/mpurse`,
		method: 'post',
		data
	})
}

export function queryOrderPayStatus(order_id) {//
	return request({
		url: '/payment/cashfree/return',
		method: 'get',
		params: { order_id }
	})
}